
.sidebar {
    flex: 1;
    height: 90vh;
    background-color: rgb(251, 251, 251);
    position: sticky;
    top: 10vh;
}


.wrapper {
    padding: 20px;
    color: #555;
}

.menu {
    margin-bottom: 10px;
}

.title {
    font-size: 13px;
    color: rgb(184, 184, 184);
}

.list {
    padding:5px;
}

.list-item {
    padding:5px;
    cursor: pointer;
    display: flex;
    border-radius: 20px;
}

.list-item.active, .list-item:hover {
    background-color: rgb(231, 231, 247);
}

.icon {
    margin-inline-end: 5px;
    font-size: 20px;
}


.link {
    text-decoration: none;
    color: inherit;
  
  }
  