.header {
    width: 100%;
    height: 10vh;
    background-color: white;
    position: sticky;
    top:0;
    z-index: 999;
}

.wrapper {
    height:100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    color: darkblue;
    cursor: pointer;
}


.top-right {
    display: flex;
    align-items: center;
}
.icon-container {
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color: #555;

}
.top-icon-badge {
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color:white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.top-avatar {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;

}