.productList {
    flex: 4;
}

.product {
    display: flex;
    align-items: center;
}

.edit {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-inline-end:20px ;
}

.delete{
    color: red;
    cursor: pointer;

}


.img {
    width:32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-inline-end: 10px;

}