.featured {
    width: 100%;
    display: flex;
    justify-content: space-between;

}

.item {
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.title {
    font-size: 20px;
}

.money-container {
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.money {
    font-size: 30px;
    font-weight: 600;
}

.money-rate {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.icon {
    font-size: 14px;
    margin-block-end: 5px;
    color: green;
}

.icon.negative {
    color: red;
}

.sub {
    font-size: 15px;
    color: gray;
}