.user {
    flex: 4;
    padding: 20px;
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.add {
    width:80px;
    border: none;
    padding: 5px;
    background-color: teal;
    border-radius: 5px;
    cursor: pointer;
    color: white;
    font-style: 16px;
}

.user-conatiner {
    display: flex;
    margin-top: 20px;
}

.show {
    flex: 1;
    margin-inline-end: 20px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.update {
    flex: 2;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    margin-inline-end: 20px;
}

.showtop {
    display: flex;
    align-items: center;
}

.showimg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-inline-end: 20px;
    object-fit: cover;
}

.showtitle {
    display: flex;
    flex-direction:column;
    margin-inline-end: 20px;
}

.showusername {
    font-weight: 600;
}

.showjob {
    font-weight: 300;
}

.showbottomtitle {
    font-size: 14px;
    font-weight: 600;
    color: rgb(179, 179, 179);
}
.showbottom {
    margin-top: 20px;
}

.showbottomcontainer {
    display: flex;
    align-items:center;
    margin: 20px 0px;
    color: #444;

}

.showbottomicon {
    font-size: 16px;

}

.showbottominfotitle {
    margin-inline-start: 10px;
}

.updateImg {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-block-end: 20px;
    border-radius: 10px;
}

.updateusertitle {
    font-size: 24px;
    font-weight: 600;
}

.updateform {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.updateItem {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    
}

.updateItem > label {
    margin-bottom: 5px;
    font-size: 14px;
}

.updateItem > input {
    border: none;
    width: 250px;
    height: 30px;
    border-bottom: 1px solid rgb(184, 184, 184);
}

.updateRight {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.updateUpload {
    display: flex;
    align-items: center;
}

.updateImgIcon {
    cursor: pointer;

}

.updateUser {
    border-radius: 5px;
    border: none;
    padding: 5px;
    cursor: pointer;
    background-color: darkblue;
    color: white;
    font-weight: 600;
}