.widget {
    flex: 1;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    padding: 20px;
    margin-inline-end: 20px;
}

.widget-small {
    font-size: 22px;
    font-weight: 600;
}

.img {
    height: 40px;
    width: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.widget-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;
}

.widget-user {
    display: flex;
    flex-direction: column;
}

.username {
    font-weight: 600;
}

.title {
    font-weight: 300;
}

.button {
    display: flex;
    align-items: center;
    border: none;
    font-weight: 400;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}
.icon {
    font-style: 16px;
    margin-inline-end: 5px;
}