.newUser {
    flex: 4;
}

.newUserForm {
    display: flex;
    flex-wrap: wrap;
}

.item {
    width: 400px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-inline-end: 20px;
}

.item > label {
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: rgb(190, 190, 190);
}

.item > input {
   height: 20px;
   padding: 10px;
   border: 1px solid gray;
   border-radius: 5px;
}

.gender > input{
    margin-top: 15px;
}

.gender > label{
    margin: 10px;
    font-size: 18px;
    color: #555;
}

.select {
    height: 40px;
    border-radius: 5px;
}

.newButton {
    width: 200px;
    border: none;
    background-color: darkblue;
    color: white;
    padding: 7px 5px;
    font-weight: 600;
    border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;
}