.widget {
    flex: 2;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    padding: 20px;
}

.title {
    font-weight: 22px;
    font-weight: 600;
}

.table {
    width: 100%;
    border-spacing: 20px;
}

.th {
    text-align: left;
}

.user {
    display: flex;
    align-items: center;
    font-weight: 600;
}

.date, .amount {
    font-weight: 300;
}
.img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-inline-end: 10px;
}

.WidgetLgBtn {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}

.WidgetLgBtn.Approved {
    background-color: #e5faf2;
    color: #3bb077;
}

.WidgetLgBtn.Declined {
    background-color: #fff0f1;
    color: #d95087;
}

.WidgetLgBtn.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;
}